@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200&display=swap);
body {
  margin: 0;
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #eceef1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.weather-app {
  display: flex;
  flex-direction: column;
  margin: 0 20px;
}

.forecast-details {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}
.weather-app-location-search {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 2%;
  margin-left: 30%;
  margin-right: 30%;
}
.error-message{
  color: red;
  font-size: large;
}


h1{
  text-align: center;
}
.forecast-summary{
  padding: 5%;
  background: #66748A;
  border-radius: 25px;
  
  color:white;
}

.forecast-summary__button{
  box-shadow: 0px 0px 0px transparent;
  border: 0px solid transparent;
  background:transparent;
  text-shadow: 0px 0px 0px transparent;
}

.forecast-summary:hover{
  background: #404756;
  color:white;
  cursor: pointer;
}

.forecast-summary:hover > .forecast-summary__button{
  color:white;
  cursor: pointer;
}

button{
  color:#66748A;
}


.forecast-summaries {
  display: flex;
  justify-content: space-between;
  text-align: center;
  margin-top: 5%;
}
.forecast-details {
  border: solid;
  border-color: #66748A;
  background-color: #aaafb9;
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  margin-left:15%;
  margin-right:15%;
}

.forecast-details-bar{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  width: 100%;
}

.forecast-details__date{
  color: #404756;
  font-size: 1.5em;
  padding: 0 5% 5% 5%;
  font-weight: 1000;
}


.wind-details{
  display: flex;
  padding-left: 15%;
}

button{
  width:100%
}


